/**
* Styles for the Floor Template
* All styles are consolidated here for easy editing by users
* Now with dynamic color support based on background image or custom colors ////
*/

@use 'portal';
@use 'stairs';

:root {
  /* Default colors (fallbacks) */
  --background-color: #000;
  --title-bg-color: rgba(20, 20, 20, 0.85);
  --content-bg-color: rgba(35, 35, 35, 0.85);
  --title-text-color: #fff;
  --content-text-color: #e6e6e6;
  --subtitle-color: #e90303;

  --title-font: 'Metamorphous', serif;
  --body-font: 'Winky Sans', sans-serif;
}

/* Variables - non-color styles remain the same */
$border-radius: 15px;
$box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
$container-width: 80%;
$container-max-width: 800px;
$padding-standard: 20px;
$padding-large: 30px;
$margin-standard: 30px;
$margin-large: 80px;

/* --------------------------------------------------------
* 1. FULL VIEWPORT COVERAGE
* -------------------------------------------------------- */
html,
html.floor-template-active,
html.floor-fullscreen {
  height: 100% !important;
  min-height: 100% !important;
  margin: 0 !important;
  padding: 0 !important;
}

body,
body.floor-template-active,
body.floor-fullscreen {
  margin: 0 !important;
  padding: 0 !important;
  width: 100% !important;
  height: 100% !important;
  min-height: 100vh !important;
  overflow-x: hidden !important;
  background: transparent !important;
  max-width: 100% !important;
  font-family: var(--body-font) !important;
}

/* --------------------------------------------------------
 * 2. HIDE WORDPRESS ELEMENTS
 * -------------------------------------------------------- */
/* Core hiding strategy */
body * {
  /* display: none !important; */
}

/* Only show our specific elements */
.spiral-tower-floor-wrapper,
.spiral-tower-floor-wrapper *,
.spiral-tower-floor-container,
.spiral-tower-floor-container *,
.spiral-tower-floor-header,
.spiral-tower-floor-title,
.spiral-tower-floor-title *,
.spiral-tower-floor-content,
.spiral-tower-floor-content * {
  display: block;
}

.spiral-tower-floor-content.has-long-content p,
.spiral-tower-floor-content.has-long-content ul,
.spiral-tower-floor-content.has-long-content ol,
.spiral-tower-floor-content.has-long-content li,
.spiral-tower-floor-content.has-long-content blockquote {
  font-size: 1.8rem !important;
}

/* Specifically hide WordPress elements */
#wpadminbar,
.admin-bar #wpadminbar,
nav,
#site-navigation,
.main-navigation,
.menu,
.menu-toggle,
.site-header,
.site-header-menu,
.site-branding,
#masthead,
.nav-menu,
.top-bar,
.navigation-top,
.primary-navigation,
.navigation,
.main-header,
.header,
#header,
.site-footer,
#colophon,
.footer,
footer {
  display: none !important;
  visibility: hidden !important;
  opacity: 0 !important;
  height: 0 !important;
  width: 0 !important;
}

/* Hide common theme containers */
body {
  #page,
  #content,
  .site,
  .site-content,
  .content-area,
  #wrapper,
  #main,
  .content-wrapper,
  .container {
    background: transparent !important;
    padding: 0 !important;
    margin: 0 !important;
    max-width: 100% !important;
    width: 100% !important;
    display: none !important;
  }
}

/* --------------------------------------------------------
* 4. CONTENT CONTAINER
* -------------------------------------------------------- */
.spiral-tower-floor-container {
  width: 80% !important;
  max-width: 800px !important;
  position: fixed !important;
  top: 50% !important;
  left: 50% !important;
  transform: translate(-50%, -50%) !important;
  z-index: 1000000 !important;
  box-sizing: border-box !important;
  transition: opacity 2s ease !important;
  opacity: 0 !important;

  /* Ensure container doesn't overlap with title */
  margin-top: 60px !important;
  /* Adjust based on title height */
  max-height: calc(100vh - 100px) !important;
  /* Adjust to leave space for title */
}

.spiral-tower-floor-content.is-scrollable::after {
  opacity: 0.7;
}

/* Content scroll bars */
@media (max-width: 768px) {
  .spiral-tower-floor-container {
    width: 90% !important;
    margin-top: 50px !important;
  }

  .spiral-tower-floor-content {
    max-height: calc(80vh - 100px) !important;
  }
}

@media (max-width: 480px) {
  .spiral-tower-floor-container {
    width: 95% !important;
    margin-top: 45px !important;
  }

  .spiral-tower-floor-content {
    max-height: calc(75vh - 90px) !important;
  }
}

/* --------------------------------------------------------
* 5. TITLE STYLING
* -------------------------------------------------------- */
.spiral-tower-floor-title {
  width: 100% !important;
  margin: 0 !important;
  border-radius: 0 !important;
  background-color: var(--title-bg-color, rgba(20, 20, 20, 0.85)) !important;
  padding: $padding-standard !important;
  box-shadow: $box-shadow !important;
  box-sizing: border-box !important;
  position: fixed !important;
  /* Fixed at the top */
  top: 0 !important;
  left: 0 !important;
  z-index: 1000001 !important;
  cursor: pointer !important;
  /* Indicate it's clickable */

  h1 {
    margin: 0 !important;
    padding: 0 !important;
    font-size: 1.5em !important;
    color: var(--title-text-color, #fff) !important;
    font-weight: bold !important;
    line-height: 1.2 !important;
    font-family: var(--title-font, 'Metamorphous', serif) !important;
    letter-spacing: -0.5px;
    text-align: center;
  }
}

.spiral-tower-floor-number {
  font-size: 0.8em !important;
  margin-bottom: 6px !important;
  font-weight: bold !important;
  color: var(--subtitle-color, #e90303) !important;
  font-family: var(--title-font) !important;
  text-align: center;
  ; /* Note: This semicolon is present in the original and left untouched */
}

/* --------------------------------------------------------
 * 6. CONTENT STYLING
 * -------------------------------------------------------- */
.spiral-tower-floor-content {
  background-color: var(--content-bg-color, rgba(0, 0, 0, 0.6)) !important;
  padding: 30px !important;
  border-radius: 15px !important;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1) !important;
  box-sizing: border-box !important;
  color: var(--content-text-color, #e6e6e6) !important;
  position: relative !important;
  max-height: calc(80vh - 120px) !important;
  /* Adjust based on title height + spacing */
  overflow-y: auto !important;

  /* Style content elements */
  p,
  ul,
  ol,
  li,
  blockquote {
    color: var(--content-text-color, #e6e6e6) !important;
    max-width: 100% !important;
    display: block !important;
    box-sizing: border-box !important;
    font-family: var(--body-font) !important;
    letter-spacing: -0.5px;
    font-size: 2rem;
  }

  /* Apply title font to headings */
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: var(--title-text-color, #fff) !important;
    max-width: 100% !important;
    display: block !important;
    box-sizing: border-box !important;
    font-family: var(--title-font) !important;
  }

  /* Allow inline elements to display properly */
  a,
  strong,
  em,
  span,
  b,
  i {
    display: inline !important;
  }

  /* Style links with accent color */
  a {
    color: var(--subtitle-color, #e90303) !important;
    text-decoration: none !important;

    &:hover {
      text-decoration: underline !important;
    }
  }
}

/* Custom scrollbar styling */
.spiral-tower-floor-content::-webkit-scrollbar {
  width: 12px !important;
}

.spiral-tower-floor-content::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.3) !important;
}

.spiral-tower-floor-content::-webkit-scrollbar-thumb {
  background-color: var(--subtitle-color, #e90303) !important;
}

.spiral-tower-floor-content::-webkit-scrollbar-thumb:hover {
  background-color: rgba(233, 3, 3, 0.8) !important;
}

/* --------------------------------------------------------
* 7. RESPONSIVE ADJUSTMENTS
* -------------------------------------------------------- */
@media (max-width: 768px) {
  .spiral-tower-floor-container {
    width: 90% !important;
    margin: 0 auto !important;
  }

  .spiral-tower-floor-title {
    padding: 15px !important;

    h1 {
      font-size: 2em !important;
    }
  }

  .spiral-tower-floor-content {
    padding: 20px !important;
    margin-bottom: 40px !important;
  }
}

@media (max-width: 480px) {
  .spiral-tower-floor-container {
    width: 95% !important;
    margin: 0 auto !important;
  }

  .spiral-tower-floor-title {
    padding: 12px !important;

    h1 {
      font-size: 1.8em !important;
    }
  }

  .spiral-tower-floor-number {
    font-size: 1em !important;
  }
}

/* --------------------------------------------------------
* 8. Animation
* -------------------------------------------------------- */
/* When title is hovered, show the container */
.spiral-tower-floor-title:hover ~ .spiral-tower-floor-container,
.spiral-tower-floor-container:hover {
  opacity: 1 !important;
}

/* --------------------------------------------------------
* 9. YouTube Background
* -------------------------------------------------------- */
#youtube-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
  pointer-events: none;

  &.audio-only {
    /* Replace display: none with this: */
    position: absolute !important;
    width: 1px !important;
    height: 1px !important;
    top: -9999px !important;
    left: -9999px !important;
    opacity: 0 !important;
    visibility: hidden !important;
    overflow: hidden !important;
    pointer-events: none !important; /* Ensure it doesn't intercept clicks */

    /* Ensure iframe inside is also hidden if necessary,
       though the above usually suffices. */
    iframe {
      /* May not be needed if parent is fully hidden */
      /* opacity: 0; */
      /* visibility: hidden; */
    }
  }
}

.youtube-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
}

#youtube-player {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100vw;
  height: 56.25vw;
  /* 16:9 aspect ratio (9/16*100) */
  min-height: 100vh;
  min-width: 177.78vh;
  /* 16:9 aspect ratio (16/9*100) */
  transform: translate(-50%, -50%);
  border: none;
}

#youtube-background.audio-only {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

#youtube-background.audio-only iframe {
  visibility: hidden;
}

/* Start invisible javascript will trigger fade in */
#youtube-background {
  opacity: 0;
  animation: youtube-fade-in 6s forwards;
}

@keyframes youtube-fade-in {
  0%,
  50% {
    opacity: 0;
  }

  51% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}


/* --------------------------------------------------------
* 9. YouTube Background 
* -------------------------------------------------------- */
#youtube-background {
  /* ... (keep existing styles like position, z-index etc) ... */
  overflow: hidden; /* Container hides iframe overflow */
}

.youtube-container {
  /* ... (keep existing styles) ... */
  overflow: hidden;
}

#youtube-player {
  /* ... (keep existing styles like position, aspect ratio calculations) ... */
  transition: transform 0.5s ease-in-out; /* Smooth transition for video scrolling */
}


/* --------------------------------------------------------
* 10. TOOLBAR
* -------------------------------------------------------- */
#toolbar {
  display: flex;
  position: fixed;
  bottom: 0;
  right: 60px;
  z-index: 9999999;
  background: rgba(0, 0, 0, .5);

  div {
    flex: 1;
    width: 30px; 
    height: 30px; 
    padding: 4px;
    display: block;
    border: 1px solid rgba(0, 0, 0, .9);
    text-align: center;
    margin-right: -1px;
  }

  #button-stairs {
  }

  /* Allow sound toggle to display */
  #button-sound-toggle,
  #button-sound-toggle { /* Note: Duplicate selector present in original */
    visibility: visible;
    opacity: 1;
    cursor: pointer;
    display: none;
    visibility: hidden;
    opacity: 0;
  }
}

.tooltip-trigger {
  position: relative;
  /* Needed for absolute positioning of the tooltip */
  cursor: help;
  /* Optional: Change cursor */
  border-bottom: 1px dotted black;
  /* Optional: Indicate it has a tooltip */
}

.tooltip-trigger::after {
  content: attr(data-tooltip);
  /* Get text from data-tooltip attribute */
  position: absolute;
  bottom: 100%;
  /* Position above the element */
  left: 50%;
  transform: translateX(-50%);
  /* Center the tooltip */
  margin-bottom: 5px;
  /* Space between element and tooltip */

  /* Styling */
  background-color: #333;
  color: #fff;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 0.9em;
  white-space: nowrap;
  /* Prevent line breaks */

  /* Hide by default */
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
  z-index: 10;
  /* Ensure it's above other elements */
}

/* Show on hover */
.tooltip-trigger:hover::after {
  opacity: 1;
  visibility: visible;
}

/* Optional: Add a small arrow */
.tooltip-trigger::before {
  content: '';
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: -0px;
  /* Position just below the main tooltip box */
  border-width: 5px;
  border-style: solid;
  border-color: #333 transparent transparent transparent;
  /* Arrow pointing down */

  /* Hide by default */
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s ease-in-out, visibility 0.2s ease-in-out;
  z-index: 11;
  /* Above the tooltip box */
}

.tooltip-trigger:hover::before {
  opacity: 1;
  visibility: visible;
}











