/* --------------------------------------------------------
* WRAPPER AND GIZMO POSITIONING
* -------------------------------------------------------- */

/* Reset core elements */
html, body {
  margin: 0 !important;
  padding: 0 !important;
  overflow: hidden !important;
  width: 100% !important;
  height: 100% !important;
}

/* 
 * The wrapper starts at its natural size (image or video dimensions)
 * and is then scaled to fit the viewport
 */
.spiral-tower-floor-wrapper {
  /* Position in the center of the viewport */
  position: fixed !important;
  top: 50% !important;
  left: 50% !important;
  transform: translate(-50%, -50%) !important; /* Center positioning */
  transform-origin: center center !important;
  
  /* Don't set width/height here - JavaScript will set based on content */
  /* The width and height will be set to match the background content's natural size */
  
  overflow: hidden !important;
  z-index: 1 !important;
  
  /* Debug outline - uncomment to see the wrapper borders */
  /* outline: 2px solid red !important; */
}

/* Background containers that fill the wrapper */
.background-container {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
  margin: 0 !important;
  padding: 0 !important;
  overflow: hidden !important;
}

/* Image background fills the container */
#image-background img#background-image {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
  object-fit: cover !important;
  object-position: center !important;
}

/* YouTube background styling */
#youtube-background {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
  overflow: hidden !important;
}

.youtube-container {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
  overflow: hidden !important;
}

#youtube-player {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
  object-fit: cover !important;
}

/* Gizmo container for positioning elements on the background */
.wrapper-floor-gizmos {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  width: 100% !important;
  height: 100% !important;
  pointer-events: none !important; /* Allow clicking through to background by default */
  
  /* Debug outline - uncomment to see the gizmo container boundaries */
  /* outline: 2px dashed blue !important; */
}

/* Individual gizmos can be positioned precisely */
.floor-gizmo {
  position: absolute !important;
  /* Don't set dimensions here - each gizmo should define its own size */
  pointer-events: auto !important; /* Make individual gizmos clickable */
  
  /* Debug styling - uncomment to see gizmo boundaries */
  /* background-color: rgba(255, 0, 0, 0.3) !important; */
  /* outline: 1px solid yellow !important; */
}

/* Overlay elements that stay fixed regardless of wrapper scaling */
.spiral-tower-floor-title,
.spiral-tower-floor-container,
#toolbar {
  z-index: 1000 !important;
  position: fixed !important;
}





/* --------------------------------------------------------
* FULLSCREEN VIEWPORT - RESIZE AWARE
* -------------------------------------------------------- */

/* Reset essential viewport elements */
html, body {
  margin: 0 !important;
  padding: 0 !important;
  overflow: hidden !important;
  width: 100% !important;
  height: 100% !important;
  max-width: 100vw !important; /* Use viewport units */
  max-height: 100vh !important; /* Use viewport units */
  box-sizing: border-box !important;
}

/* Core wrapper styling - fixed to ensure it covers the viewport */
.spiral-tower-floor-wrapper {
  position: fixed !important; /* Fixed rather than absolute to ensure it stays put */
  top: 0 !important;
  left: 0 !important;
  right: 0 !important; /* Using right and bottom as well as top and left */
  bottom: 0 !important;
  width: 100% !important;
  height: 100% !important;
  margin: 0 !important;
  padding: 0 !important;
  overflow: hidden !important;
  z-index: 1 !important;
  
  /* Ensure no transforms are applied */
  transform: none !important;
  
  /* Ensure proper sizing calculation */
  box-sizing: border-box !important;
}

/* Background containers */
.background-container {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  width: 100% !important;
  height: 100% !important;
  margin: 0 !important;
  padding: 0 !important;
  overflow: hidden !important;
  
  /* Ensure proper sizing calculation */
  box-sizing: border-box !important;
}

/* Image background - use object-fit for proper resize handling */
#image-background {
  img#background-image {
    position: absolute !important;
    top: 0 !important;
    left: 0 !important;
    width: 100% !important;
    height: 100% !important;
    margin: 0 !important;
    padding: 0 !important;
    
    /* These properties ensure proper resize handling */
    object-fit: cover !important;
    object-position: center !important;
    
    /* Ensure no transforms are applied */
    transform: none !important;
  }
}

/* YouTube background */
#youtube-background {
  position: absolute !important;
